import { Component } from '@angular/core';
import { PopoverController } from 'ionic-angular';
import { CreateTransactionPage } from '../transaction/transaction';
import { CreateTimesheetPage } from '../timesheet/timesheet';
import { CreateDipReadingPage } from '../dipreading/dipreading';
import { CreateTankToTankPage } from '../tanktotank/tanktotank';

@Component({
    selector: 'page-tabs',
    templateUrl: 'tabs.html'
})
export class TabsPage {

    public transaction: any;
    public timesheet: any;
    public dipreading: any;
    public tanktotank: any;

    constructor(public popoverController: PopoverController) {
        this.transaction = CreateTransactionPage;
        this.timesheet = CreateTimesheetPage;
        this.dipreading = CreateDipReadingPage;
        this.tanktotank = CreateTankToTankPage;
    }
}


